import { Component, OnInit } from '@angular/core';
import { PermissionService } from '../../../../auth/_services/permission.service';
import { CategoryService } from '../../../../auth/_services/category.service';
import { ToastrService } from 'ngx-toastr';
import { SelectModule } from 'ng2-select';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';

@Component({
    selector: 'app-user-group',
    templateUrl: "user-group.component.html"
})

export class UserGroupComponent implements OnInit {


    constructor(private imgBaseUrl: ScriptLoaderService, private toastr: ToastrService, private _categoryService: CategoryService, private _permissionService: PermissionService) { }
    public userGroupData;



    ngOnInit() {

        this._permissionService.getAllUserGroup().subscribe(
            data => {
                this.userGroupData = data.success;
            }
        );
    }
}
